import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Paper, withStyles } from '@material-ui/core';

import api from '../../services/apiTasks';
import styles from './styles';

import { getTeam } from '../../store/modules/team/actions';
import LoadingComponent from '../../components/LoadingComponent';

function SingleTaskPage({ classes, match }) {
  const dispatch = useDispatch();
  const { id } = match.params;
  const team = useSelector(state => state.team.team);
  const loadingUser = useSelector(state => state.user.loading);
  const loadingTeam = useSelector(state => state.team.loading);
  const [loading, setLoading] = useState(true);
  const [task, setTask] = useState(null);
  const [answer, setAnswer] = useState(null);

  useEffect(() => {
    async function getTaks() {
      setLoading(true);
      const response = await api.get(
        `/tasks/detail/${team.teamId}?type=${team.type} &taskId=${id}`
      );

      const { task, answer } = response.data[0];

      setTask(task);
      setAnswer(answer);
      setLoading(false);

      console.log(response.data[0]);
    }

    if (!team) {
      if (!loadingUser) {
        dispatch(getTeam());
      }
    } else {
      getTaks();
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loadingUser, loadingTeam]);

  return (
    <Paper className={classes.root}>
      {loading || loadingTeam || loadingUser ? (
        <LoadingComponent />
      ) : (
        <h1>{task.title}</h1>
      )}
    </Paper>
  );
}

export default withStyles(styles)(SingleTaskPage);
