/* eslint-disable react/prop-types */
/* eslint-disable no-nested-ternary */
import React, { useState, useEffect } from 'react';
import { Grid, Typography, withStyles, Divider } from '@material-ui/core';
import LinesEllipsisLoose from 'react-lines-ellipsis/lib/loose';
import history from '../../../services/history';

import page from './styles';

const { styles, Root } = page;

// import { Container } from './styles';

function TaskComponent({ classes, task }) {
  const [color, setColor] = useState('grey');

  useEffect(() => {
    if (task.answer) {
      if (task.answer.status === 'Corrigida') {
        setColor('#ed5565');
      } else if (task.answer.status === 'Recorreao') {
        setColor('#23c6c8');
      } else {
        setColor(`#1ab394`);
      }
    } else {
      setColor('#f8ac59');
    }
  }, [task.answer]);

  return (
    <Grid
      item
      xs={12}
      md={4}
      onClick={() => history.push(`tasks/${task.task._id}`)}
    >
      <Root color={color}>
        <Typography variant="h5" ccomponent="h3" className={classes.number}>
          {task.task.number}
        </Typography>

        <Divider orientation="vertical" className={classes.divider} />

        <LinesEllipsisLoose
          text={task.task.title}
          className={classes.name}
          maxLine="1"
          lineHeight="16"
        />

        {task.answer && task.answer.status === 'Corrigida' ? (
          <>
            <Divider orientation="vertical" className={classes.divider} />

            <Typography
              variant="h5"
              ccomponent="h3"
              className={classes.percent}
            >
              {task.answer.grade}%
            </Typography>
          </>
        ) : null}
      </Root>
    </Grid>
  );
}

export default withStyles(styles)(TaskComponent);
